.heroContainer {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: all 1s;
}

.videoBg{
  user-select: none;
  pointer-events: none;
}

.content {
  display: block;
  text-align: justify;
  padding: 1rem 2rem;
  color: #fff;
  background: rgba(0, 0, 0, 0.25);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  border-radius: 10px;
  position: fixed;
  bottom: 10px;
  margin: 1rem;
}

.heroContainer:last-child {
  align-items: center;
}
video {
  object-fit: cover;
  height: 100%;
  width: 100%;
  position: fixed;
}

.mainHeading {
  font-size: 150px;
}

.mainHeading ~ p {
  font-size: 40px;
}

.reveal-text {
  color: #fff;
  opacity: 0;
  transform: translateY(50px);
  animation: revealAnimation 3s forwards;
}
section {
  min-height: 100vh;
}
@media only screen and (max-width: 768px) {
  .mainHeading {
    font-size: 80px;
  }
  .mainHeading >p
    {
    font-size: 20px;
  }
  .heroContainer {
    align-items: center;
  }
}
